interface ObjType {
  item: string;
  stuffs: {
    [index: string]: string; // Index Signature
    title: string;
    author: string;
    date: string;
    masterpiece: string;
  }[];
}

export default Vue.extend({
  data(): ObjType {
    return {
      item: "title",
      stuffs: [
        {
          title: "My First Vue Project with Vuetify",
          author: "Shin Leejong",
          date: "2021-06-24",
          masterpiece: "걸작",
        },
        {
          title: "Vuetify is so awesome",
          author: "Shin Leejong",
          date: "2021-06-23",
          masterpiece: "걸작",
        },
        {
          title: "한글로도 뭔가 적어야지",
          author: "신이종",
          date: "2021-06-24",
          masterpiece: "쓰레기",
        },
        {
          title: "미래에서 온 소년",
          author: "신이종",
          date: "2022-06-24",
          masterpiece: "걸작",
        },
        {
          title: "A Cowboy From The Past",
