  components: {},
  methods: {
    sort(selection: string, reversion: boolean) {
      if (reversion === true)
        this.stuffs.sort((a, b) => (a[selection] < b[selection] ? 1 : -1));
      else this.stuffs.sort((a, b) => (a[selection] < b[selection] ? -1 : 1));
    },
  },
